<template>
  <ul class="calendar-form__periods">
      <period
        v-for="(period, idx) in periods"
        :key="`period-${idx}`"
        :checkbox_name="`period-${period.id}`"
        :checkbox_value="period.id"
        :is_checked="period.id === selected_period"
        :label="period.text"
        v-model="selected_period"
      />
  </ul>
</template>

<script>
import Period from './CalendarPeriod.vue';

export default {
  name: 'CalendarPeriods',

  components: {
    Period,
  },

  props: {
    period_value: {
      type: String,
      default: 'today',
    },
  },

  data: () => ({
    periods: [
      {
        id: 'all',
        text: ' ',
      },
      {
        id: 'today',
        text: '',
      },
      {
        id: 'yesterday',
        text: '',
      },
      {
        id: '7',
        text: ' 7 ',
      },
      {
        id: '30',
        text: ' 30 ',
      },
      {
        id: 'thisMonth',
        text: '  ',
      },
      {
        id: 'lastMonth',
        text: ' ',
      },
    ],
  }),

  computed: {
    selected_period: {
      get() {
        return this.period_value;
      },
      set(val) {
        this.$emit('select-period', val);
      },
    },
  },
};
</script>
